import CommonTitle from 'components/CommonTitle'
import custHeader from  'components/custHeader'
import FormItem from 'components/FormItem'
import ButtonWrap from "components/ButtonWrap"
    export default {
        components: {
            CommonTitle,
            custHeader,
            FormItem,
            ButtonWrap
        },
        methods:{
            mySubmit: function(){
                console.log(this.formVals)
                axiosUtil.savePersonInfo(this.formVals).then(data => {
                    if(data.code == 0){
                        console.log(data.msg);

                    }else{
                        //toast
                        console.log(data.msg)
                    }
                
                })
            },
            updateFormVals: function(val){
                this.formVals[val.name] = val.value;
            }
        },
        created(){
            let _this = this;
            axiosUtil.getEducation().then(data => {
                console.log(data);
                if(data.code == 0){
                    _this.xlOptions = data.data.enumVoList || [];
                }else{
                    //toast
                }
                
            });
            axiosUtil.getMarital().then(data => {
                console.log(data);
                if(data.code == 0){
                    _this.marriageOptions = data.data.enumVoList || [];
                }else{
                    //toast
                }
                
            });
            axiosUtil.getPersonInfo().then(data => {
                console.log(data);
                if(data.code == 0){
                    let obj = data.data || {};
                    _this.formVals = obj;
                }else{
                    //toast
                }
                
            })
        },
        data(){
            return{
                title: '洋葱金融系统',
                xlOptions: [],
                marriageOptions:[],
                formVals: {
                    address:"",
                    education: "",
                    liveDuration: "",
                    maritalStatus: "",
                    qq: ""
                }
            }
        }
    }



